.contacts__headlines {
  display: flex;
  justify-content: space-between;

  .set-font(15px, 400, 19px, #fff);

  margin-bottom: 22px;
  border-bottom: 1px solid @lightgrey;
}

.contacts__btn {
  position: relative;
}

.contacts__name {
  //position: relative;

  color: @lightgrey;
  padding: 10px 0;
  transition: color 0.3s ease;
  cursor: pointer;

  &:hover {
    color: @hover;

    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 10px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 7px 10px;
      border-color: transparent transparent @hover; /* Меняем цвет нижней рамки */
      // transform: translateY(-2px); /* Двигаем треугольник вверх */
    }

    &::after {
      position: absolute;
      content: "";
      bottom: -1.5px;
      left: 10px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 7px 10px;
      border-color: transparent transparent @background; /* Меняем цвет нижней рамки */
      // transform: translateY(-2px); /* Двигаем треугольник вверх */
    }
  }
}

.contacts__triangle {
  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 10px;
    border-color: transparent transparent @lightgrey;
  }

  &::after {
    position: absolute;
    content: "";
    bottom: -1.5px;
    left: 10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7px 10px;
    border-color: transparent transparent @background;
  }
}

.contacts__container {
  display: flex;
  justify-content: center;
  height: 140px;
}

.contacts__text {
  display: none;

  .set-font(12px, 300, 16px, #fff);

  text-align: center;
}

.contacts__text.active {
  display: block;
}

.contacts__text p {
  margin-bottom: 15px;
}
