.advantages-card__item {
  position: relative;

  padding: 24px;

  box-shadow: 0px 4px 30px 13px rgba(0, 0, 0, 0.25);
  border-radius: 25px;
  background-color: rgb(192 192 192 / 22%);
  pointer-events: none; /* Фон не будет перехватывать события */
}

.advantages-card__title {
  .set-font(20px, 400, 25px, #fff);

  margin: 0 auto 60px;
}

.advantages-card__description {
  .set-font(16px, 300, 25px, @lightgrey);
}

.advantages-card__img1 {
  .insert__img(75px, -18px, "../img/comp.png", 150px, 139px);
}

.advantages-card__img2 {
  .insert__img(35px, -12px, "../img/hands.png", 160px, 156px);
}

.advantages-card__img3 {
  .insert__img(10px, -33px, "../img/heart.png");
}

.advantages-card__img4 {
  .insert__img(15px, -35px, "../img/lock.png");
}
