.main-nav {
  position: fixed;
  overflow: hidden;
  z-index: 2;

  top: 100px;
  left: 50%;
  transform: translateX(-50%);

  width: 390px;
  height: 0;

  color: #fff;

  transition: height 0.5s ease-in-out;

  background-color: rgba(0, 0, 0, 60%);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid @darkgrey;
}

.main-nav--open {
  height: 620px;
}

.main-nav__wrapper {
  margin: 0 30px;
  padding-top: 125px;
  width: @mobile-width;
}

.main-nav__list {
  margin-bottom: 64px;
}

.main-nav__item {
  text-align: center;
  margin-bottom: 70px;
}

.main-nav__text {
  .set-font(20px, 300, 24px);
  text-align: center;
}

.main-nav__btn {
  .set-font(20px, 300, 24px, #fff);
  margin-bottom: 180px;
}
