.safety__wrapper {
  overflow: hidden;

  margin: 31px auto 42px;
  padding: 0 30px;

  width: @mobile-width;
}

.safety__title {
  .set-font(32px, 400, 35px, #fff);

  margin: 0 auto 45px;
}

.safety__container {
  position: relative;

  padding: 14px 32px 18px;

  border-radius: 25px;
  background-color: rgb(192 192 192 / 22%);
  box-shadow: 0px 4px 30px 13px rgba(0, 0, 0, 0.25);

  &::before {
    .insert__img(130px, -50px, "../img/logo-kasper.png", 134px, 134px);
  }
}

.safety__description {
  .set-font(20px, 300, 30px, @lightgrey);
}
