/* BLOCKS */
.button-toggle {
  position: relative;
  display: inline-block;
  z-index: 2;
  margin: 10px 10px;
  width: 30px;
  height: 30px;
  transition: all 0.3s ease;
  background-color: #000000;
  border: none;
  cursor: pointer;
}
.button-toggle span {
  width: 5px;
  height: 5px;
  background-color: #fff;
  display: block;
  border-radius: 50%;
  position: absolute;
}
.button-toggle:hover span {
  transform: scale(1.2);
  transition: 350ms cubic-bezier(0.8, 0.5, 0.2, 1.4);
}
.button-toggle span:nth-child(1) {
  left: 0;
  top: 0;
}
.button-toggle span:nth-child(2) {
  left: 12px;
  top: 0;
}
.button-toggle span:nth-child(3) {
  right: 0;
  top: 0;
}
.button-toggle span:nth-child(4) {
  left: 0;
  top: 12px;
}
.button-toggle span:nth-child(5) {
  left: 12px;
  top: 12px;
}
.button-toggle span:nth-child(6) {
  right: 0px;
  top: 12px;
  background-color: #7DE1E1;
}
.button-toggle span:nth-child(7) {
  left: 0px;
  bottom: 0px;
}
.button-toggle span:nth-child(8) {
  left: 12px;
  bottom: 0px;
}
.button-toggle span:nth-child(9) {
  right: 0px;
  bottom: 0px;
  background-color: #E5585F;
}
.button-toggle.open {
  transform: rotate(180deg);
  cursor: pointer;
  transition: 0.2s cubic-bezier(0.8, 0.5, 0.2, 1.4);
}
.button-toggle.open span {
  border-radius: 50%;
  transition: 0.5s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  transition-delay: 200ms;
}
.button-toggle.open span:nth-child(2) {
  left: 6px;
  top: 6px;
}
.button-toggle.open span:nth-child(4) {
  left: 6px;
  top: 18px;
}
.button-toggle.open span:nth-child(6) {
  right: 6px;
  top: 6px;
}
.button-toggle.open span:nth-child(8) {
  left: 18px;
  bottom: 6px;
}
